.wrapper {
  padding-top: 50px;
  background: #ECEFF1;
  height: 100%;
  display: flex;
  flex-flow: row wrap;
  padding: 20px;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

/*
 *  CSS for the NavBar component
 */

.clearfix::after {
    content: "";
    clear: both;
    display: table;
}

.clicky-nav-bar {
    background: darkslateblue;
    color: white;
    width: 100%;
    height: auto;
    padding: 10px 0px;
    /*
    position: fixed;
    top: 0;
    */
}

.clicky-name {
    font-size: 3vw;
    font-weight: 900;
}

.clicky-nav-bar-name,
.clicky-nav-bar-msg,
.clicky-nav-bar-results {
    display: inline-block;
    text-align: center;
    width: 33%;
}

.clicky-msg,
.clicky-results {
    font-size: 2vw;
}

a {
    color: white;
    text-decoration: none;
}
/*
 *  CSS for the Header component
 */

.clicky-header {
    background-color: darkslategray;
    color: white;
    font-size: 2vw;
    text-align: center;
    padding: 10px 0;
    /*
    margin-top: 60px;
    */    
}
/*
 *  CSS for the SuperHero Card components
 */

.card {
  background: #fff;
  border-radius: 2px;
  height: 200px;
  margin: 3rem;
  width: 150px;
  box-shadow: 0 3px 6px #999, 0 3px 6px #999;
  -webkit-transition: -webkit-transform .2s;
  transition: -webkit-transform .2s;
  transition: transform .2s;
  transition: transform .2s, -webkit-transform .2s;
  float:left;
}

img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

